body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-color{
  background: #0f0c29;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #24243e, #6b8cdf, #3964f1); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.general-bg {
  background: #667db6;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #6b8cdf, #f5f7f8, #f5f7f8, #6b8cdf); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.projects-grid {
  display: -webkit-flex;
  display: flex;
}

/* Resume Page css */
.resume-left-col {
  text-align: center;
}

.resume-left-col hr {
  margin: 0 auto;
}

.resume-right-col {
  background: #27221F;
  color: white;
  padding: 2em;
}

.resume img{
  margin-top: 3em;
  border: 4px solid rgba(139, 136, 136, 0.466);
  border-radius: 5em;
}

.resume h5{
  font-weight: bold;
}

.resume h5+p {
  font-style: italic;
}

/* about page css */

.about-body{
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 2em;
}

.about-grid {
  -webkit-justify-content: center;
          justify-content: center;
  width: 80%;
  height: 550px;
  color: black;
  margin-bottom: 20px;
}

/* contact page css */

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 80%;
  height: 550px;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: sans-serif;
}

.contact-grid p {
  font-family: sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 66px;
  padding-left: 3rem;
  padding-right: 3rem;
}

/* contact page css */

.avatar-img {
  height: 250px;
  padding-top: 7em;
}

.banner-text {
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 75%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}
